/* Core */
import { Component, ChangeDetectorRef, NgZone, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { IonicPage, NavController, NavOptions, NavParams } from 'ionic-angular';
import { DomSanitizer } from '@angular/platform-browser';

/* Services */
import { AuthService } from '../../app/services/auth.service';
import { ClipsService } from '../../app/services/clips.service';
import { LoaderService } from '../../app/services/loader.service';
import { ToastService } from '../../app/services/toast.service';
import { FavoriteClip } from '../../app/models/favorite-clip.model';

@IonicPage({
  name: 'favorite-clips',
  segment: 'favorite-clips'
})
@Component({
  selector: 'page-favorite-clips',
  templateUrl: 'favorite-clips.html'
})
export class FavoriteClipsPage implements OnInit {
  private ngZone: NgZone;
  private sanitizer: DomSanitizer;

  public favoriteClips: FavoriteClip[] = [];

  constructor(
    public navCtrl: NavController,
    private _sanitizer: DomSanitizer,
    private _ngZone: NgZone,

    /* Services */
    private authService: AuthService,
    private clipsService: ClipsService,
    private loaderService: LoaderService,
    private toastService: ToastService) {
    this.sanitizer = _sanitizer;
    this.ngZone = _ngZone;
  }

  ionViewCanEnter() {
    let that = this;
    let isAuthenticated = this.authService.isAuthenticated();

    if (!isAuthenticated) {
        that.toastService.show('Must be logged in to access this page.');
    }

    return isAuthenticated;
  }

  ngOnInit(): void {
    this.getFavorites();
  }

  getFavorites() {
    let that = this;
    let promise = this.clipsService.getFavorites();

    promise.then(function(favoriteClips: FavoriteClip[]) {
      favoriteClips.forEach(function(favoriteClip: FavoriteClip) {
        that.clipsService.getBySlug(favoriteClip.slug).then(function(clip) {
          favoriteClip.clip = clip;
          that.ngZone.run(() => {
            that.favoriteClips.push(favoriteClip);
          });
        }).catch(function(err) {});
      });
    });

    that.loaderService.show(promise);
    that.toastService.onFailure(promise);
  }

  goToClip(slug) {
    this.navCtrl.push('clip', {slug: slug});
  }

  goToProfile() {
    this.navCtrl.push('profile');
  }
}



